import React, {useState} from 'react';
import prophetpic from './assets/ezekiel.jpg';
import './App.css';
import Prophet from "./Prophet"
import { Animated } from "react-animated-css";

function App() {
    const [visible, setVisible] = useState(true);
    const [fading, setFading] = useState(false);

    function hideMe() {
        setFading(true);
        setTimeout(() => setVisible(false), 650);
    }

    return (
            <div id="container">
                <Animated animationOut="fadeOutUp" isVisible={!fading} style={visible ? null : { display: "none" }}>
                    <div id="entry-welcome">
                        <div id="welcome-middle">
                            <img src={prophetpic}/>
                            <h1>The Minor Prophets</h1>
                            <p>made by Matteo Grilla</p>
                            <i className="fas fa-arrow-circle-down" onClick={hideMe}></i>
                        </div>
                    </div>
                </Animated>
                <Prophet id="prophet" vis={!visible ? true : false}/>
            </div>
    );
}

export default App;
