import React from 'react';

import mpContext from "./data/mpContext.json";
import apocalypticProphecy from "./data/apocalypticProphecy.json";

import hosea from "./data/hosea.json";
import joel from "./data/joel.json";
import amos from "./data/amos.json";
import obadiah from "./data/obadiah.json";
import jonah from "./data/jonah.json";
import micah from "./data/micah.json";

import nahum from "./data/nahum.json";
import habakkuk from "./data/habakkuk.json";
import zephaniah from "./data/zeph.json";
import haggai from "./data/haggai.json";
import zechariah from "./data/zechariah.json";
import malachi from "./data/malachi.json";

export default class Prophet extends React.Component {

    selectedFile = hosea;

    constructor(props) {
        super(props);
        this.toggleOverlay = this.toggleOverlay.bind(this);
        this.state = {selector: 1, active: false};
    }

    toggleOverlay() {
        const currentState = this.state.active;
        this.setState({ active: !currentState});
    }

    render() {
        // Change selected file based on index
        switch(this.state.selector) {
            case 1:
                this.selectedFile = mpContext
                break;
            case 2:
                this.selectedFile = hosea;
                break;
            case 3:
                this.selectedFile = joel;
                break;
            case 4:
                this.selectedFile = amos;
                break;
            case 5:
                this.selectedFile = obadiah;
                break;
            case 6:
                this.selectedFile = jonah;
                break;
            case 7:
                this.selectedFile = micah;
                break;
            case 8:
                this.selectedFile = nahum;
                break;
            case 9:
                this.selectedFile = habakkuk;
                break;
            case 10:
                this.selectedFile = zephaniah;
                break;
            case 11:
                this.selectedFile = haggai;
                break;
            case 12:
                this.selectedFile = apocalypticProphecy;
                break;
            case 13:
                this.selectedFile = zechariah;
                break;
            case 14:
                this.selectedFile = malachi;
                break;
        }

        return(
            (this.selectedFile.info_slide) ?

                <div id="info-wrapper" className={this.props.vis ? null : 'hide'}>
                    <div>
                        <h1>{this.selectedFile.title}</h1>
                        <div className="info-wrapper-item">
                            <h3>{this.selectedFile.sec_one_title}</h3>
                            <p>{this.selectedFile.sec_one}</p>
                        </div>
                        <div className="info-wrapper-item">
                            <h3>{this.selectedFile.sec_two_title}</h3>
                            <p>{this.selectedFile.sec_two}</p>
                        </div>
                        <div className="info-wrapper-item">
                            <h3>{this.selectedFile.sec_three_title}</h3>
                            <p>{this.selectedFile.sec_three}</p>
                        </div>
                        <div className="info-wrapper-item">
                            <h3>{this.selectedFile.sec_four_title}</h3>
                            <p>{this.selectedFile.sec_four}</p>
                        </div>
                    </div>
                    <div id="cycle-wrapper">
                        <div>
                            <i className={"fas fa-arrow-left " + (this.state.selector == 1 ? 'hide-arrow' : 'show-arrow')} onClick={() => {
                                if (this.state.selector > 1) {
                                    this.setState({selector: (this.state.selector - 1)});
                                }
                            }}></i>
                            {/*{this.state.selector}*/}
                            <i className={"fas fa-arrow-right " + (this.state.selector == 14 ? 'hide-arrow' : 'show-arrow')} onClick={() => {
                                if (this.state.selector < 14) {
                                    this.setState({selector: (this.state.selector + 1)});
                                }
                            }}></i>
                        </div>
                    </div>
                </div>
                :
            <div id="prophet-wrapper" style={this.props.style}>
                <div id="overlay" className={this.state.active ? 'ol-show': null} onClick={this.toggleOverlay}>
                    <div id="overlay-container">
                        <div id="overlay-info-container">
                            <img rel="preload" as="image" src={process.env.PUBLIC_URL + this.selectedFile.pic_url}/>
                            <p>{this.selectedFile.pic_cap}</p>
                        </div>
                    </div>
                </div>
                <div id="prophet-info-container">
                    <div id="prophet-header">
                        <img rel="preload" as="image" src={process.env.PUBLIC_URL + this.selectedFile.headshot_url} id="prophet-headshot"/>
                        <h1>{this.selectedFile.name}</h1>
                        <p id="timeframe"><i>{this.selectedFile.timeframe}</i></p>
                        <p id="key-verse">
                            {this.selectedFile.key_verse}
                            <br/>
                            <i>{this.selectedFile.key_verse_ref}</i>
                        </p>
                    </div>
                    <div id="prophet-bio">
                        <div id="prophet-bio-left">
                            <img rel="preload" as="image" src={process.env.PUBLIC_URL + this.selectedFile.pic_url} onClick={this.toggleOverlay}/>
                            <span>{this.selectedFile.pic_cap}</span>
                        </div>
                        <div id="prophet-bio-right">
                            <div id="prophet-bio-paragraph">
                                <p>{this.selectedFile.prophet_bio_1}</p>
                                <p>{this.selectedFile.prophet_bio_2}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="cycle-wrapper">
                    <div>
                        <i className={"fas fa-arrow-left " + (this.state.selector == 1 ? 'hide-arrow' : 'show-arrow')} onClick={() => {
                            if (this.state.selector > 1) {
                                this.setState({selector: (this.state.selector - 1)});
                            }
                        }}></i>
                        {/*{this.state.selector}*/}
                        <i className={"fas fa-arrow-right " + (this.state.selector == 14 ? 'hide-arrow' : 'show-arrow')} onClick={() => {
                            if (this.state.selector < 14) {
                                this.setState({selector: (this.state.selector + 1)});
                            }
                        }}></i>
                    </div>
                </div>
            </div>
        );
    }
}
